import { useDispatch } from 'react-redux';
import { setPersonToFavorite, removePersonFromFavorites } from '@store/actions';

import iconFavorite from './img/favorite.svg';
import iconFavoriteFull from './img/favorite-full.svg';

import styles from './PersonPhoto.module.css';

const PersonPhoto = ({
    personId,
    personPhoto,
    personName,
    personFavorite,
    setPersonFavorite
}) => {
    const dispatch = useDispatch();

    const dispatchFavoritePeople = () => {
        if (personFavorite) {
            dispatch(removePersonFromFavorites(personId));
            setPersonFavorite(false);
        } else {
            dispatch(setPersonToFavorite({
                [personId]: {
                    name: personName,
                    img: personPhoto
                }
            }));
            setPersonFavorite(true);
        }
    }

    return (
        <>
            <div className={styles.container}>
                <img className={styles.photo} src={personPhoto} alt={personName} />
                <img
                    src={personFavorite ? iconFavoriteFull : iconFavorite}
                    onClick={dispatchFavoritePeople}
                    className={styles.favorite}
                    alt="Add to favorite"
                />
            </div>
        </>
    )
}

export default PersonPhoto;
